.main-contacto{
    .title{
        text-align: center;
        margin: $separacion 0;
    }
}
.webp .contacto__img{
    background-image: url('../img/formulario.webp');
}
.no-webp .contacto__img{
    background-image: url('../img/formulario.jpg');
}

.contacto-container{
    margin-bottom: $separacion;
    .contacto__img{
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        height: 40rem;
        max-width: 90%;
        margin: 0 auto;
        border-radius: 1rem;
    }
}
.prueba{
    height: max-content;
    padding: $separacion / 2;
    background-color: $colorFondoElementos;
    max-width: 85%;
    margin: -3rem auto 0 auto;
    box-shadow: 0px 0px 10px 1px rgba(0, 0, 0, 0.9);
    border-radius: 1rem;

    .campo{
        margin-bottom: 3rem;
        display: flex;
        flex-direction: column;
        gap: 1rem;
        .campo__label{
            color: $colorText;
            font-size: 2rem;
        }
        .campo__field{
            background-color: $grisClaro;
            border: none;
            width: 100%;
            height: 3.5rem;
            font-size: 2rem;
            color: $blanco;
            border-radius: 1rem;
        }
    }

    #texto{
        height: auto;
    }
    .enviar-container{
        display: flex;
        justify-content: flex-end;
        .btn{
            @include btn;
            position: relative;
            border: none;
            color: $colorText;
            font-size: 2rem;
        }
    }

}

.atencion{
    background-color: #eb2f06;
    height: max-content;
    padding: 1rem 1rem;
    margin-bottom: 2rem;
    border-radius: 1rem;
    .atencion__title{
        font-size: 2rem;
        margin-bottom: .5rem;
    }
    .atencion__info{
        font-size: 1.6rem;
    }
}
