.bg-trabajos{
    background-color: $colorFondoElementos;


}
.trabajos{
    padding: $separacion 0 10rem 0;
    margin: $separacion auto;
    position: relative;

    h2{
        text-align: center;
        margin-bottom: $separacion;
    }

    .servicios__btn-container{
        @include btn;
    }
}
.trabajos-cards-container{

    @include desktop{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: $separacion / 2;
    }

    @include tablet{
        display: grid;
        grid-template-columns: 1fr 1fr;
        column-gap: $separacion / 2;
    }

    @include telefono{
        display: grid;
        grid-template-columns: 1fr;
        column-gap: $separacion / 2;
    }

    .trabajos__card-container{
      border-bottom: 1px solid $grisClaro;

      &:last-of-type{
        border-bottom: none;
      }

      @include tablet{
        border-bottom: none;
        &:last-of-type{
          grid-area: 2 / 1 / 3 / 3;
          width: 50%;
          margin: 0 auto;
        }
      }

      @include desktop{
        border-bottom: none;
      }

    }

    .trabajos__card{
        padding: $separacion / 2 0;
        display: flex;
        flex-direction: column;
        align-items: center;
        transition: all 100ms ease-in-out;

        &:hover{
          background-color: $grisClaro;
          border-radius: 1rem;
          padding: $separacion / 2 .5rem;
        }

        @include tablet{
          border-bottom: none;

          &:hover{
            background-color: $grisClaro;
            box-shadow: -5px 5px rgba(0, 0, 0, 0.5);
            border-radius: 1rem;
            padding: $separacion / 2 1rem;
            position: relative;
            bottom: .5rem;
            left: .5rem;
          }
        }

        @include desktop{
          border-bottom: none;

          &:hover{
            background-color: $grisClaro;
            box-shadow: -5px 5px rgba(0, 0, 0, 0.5);
            border-radius: 1rem;
            padding: $separacion / 2 1rem;
            position: relative;
            bottom: .5rem;
            left: .5rem;
          }
        }

        .trabajos__card__title{
          margin: $separacion / 2 0;
        }
    }

    .container-img__trabajo-cards--1{
      @include indexTrabajos('../img/paginas-normal/1.webp');
    }

    .container-img__trabajo-cards--2{
      @include indexTrabajos('../img/paginas-normal/2.webp');
    }

    .container-img__trabajo-cards--3{
      @include indexTrabajos('../img/paginas-normal/3.webp');
    }
}
