.header{
  border-bottom: 2px solid $colorFondo;
  box-shadow: 0 .3rem .5rem $colorFondo;

    @include tablet{
      position: sticky;
      top: 0;
      width: 100%;
    }

    @include desktop{
      position: sticky;
      top: 0;
      width: 100%;
    }

    .nav-bg{
        padding: $separacion / 2 0;
        background-color: $colorFondoElementos
    }
}
.nav{
    .nav__btn{
        transition: all 200ms ease-out;
        &:hover{
            border-bottom: 2px solid $azul;
            padding-bottom: .3rem;
        }
    }
}
.active{
    border-bottom: 2px solid $azulClaro;
    padding-bottom: .3rem;
}
.main{
    margin-bottom: $separacion;

    .title-main{
        text-align: center;
        margin: $separacion 0;
    }

    .main-grid{
        display: flex;
        flex-direction: column-reverse;

        @include tablet{
            display: grid;
            grid-template-columns: 2fr 1fr;
            column-gap: $separacion;
        }

        @include desktop{
            display: grid;
            grid-template-columns: 2fr 1fr;
            column-gap: $separacion;
        }

        .autenticar{
            margin-bottom: $separacion;

            @include tablet{
                margin: 0;
            }

            @include desktop{
                margin: 0;
            }
        }
    }

}