.main-trabajos{
  margin-bottom: $separacion;

  .title{
      text-align: center;
      margin: $separacion 0;
  }

  .main-trabajos-bg{
    background-color: $colorFondoElementos;
  }

  .trabajos-container{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $separacion / 2;
    padding-bottom: $separacion;
    @include tablet{
      grid-template-columns: 1fr 1fr;
    }
    @include telefono{
      display: flex;
      flex-direction: column;
    }
  }
}
.trabajo-card{
  padding-top: $separacion;

  @include telefono{
    border-top: 1px solid $grisClaro;

    &:first-of-type{
      border-top: none;
    }
  }

  .trabajo-card__img-container{
    height: 18rem;
    overflow: hidden;
    background-position: top center;
    background-repeat: no-repeat;
    background-size: cover;
    border-radius: 1rem;
    @include tablet{
      height: 21rem;
    }
    @include telefono{
      height: 23rem;
    }
  }

  .a{
    background-image: url('../img/Paginas-normal/1.webp');
  }
  .b{
    background-image: url('../img/Paginas-normal/2.webp');
  }
  .c{
    background-image: url('../img/Paginas-normal/3.webp');
  }
  .d{
    background-image: url('../img/Paginas-normal/4.webp');
  }
  .e{
    background-image: url('../img/Paginas-normal/5.webp');
  }
  .f{
    background-image: url('../img/Paginas-normal/6.webp');
  }
  .g{
    background-image: url('../img/Paginas-normal/7.webp');
  }
  .h{
    background-image: url('../img/Paginas-normal/8.webp');
  }
  .i{
    background-image: url('../img/Paginas-normal/9.webp');
  }

  .trabajo-card__title{
    margin: $separacion / 2 0;
  }

  .btn-container{
    margin: $separacion / 2 0;

    .trabajo-card__btn-principal{
      padding: 1.5rem 1.5rem;
      background-color: $azul;
      border-radius: 1rem;
      transition: all 100ms ease-in-out;
      &:hover{
        background-color: $azulClaro;
        cursor: pointer;
      }
    }
  }
}
