.cert-index{
    padding: 0;
    .cert-bg__index{
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        column-gap: $separacion / 2;
        align-items: center;
        @include tablet{
            grid-template-columns: repeat(2, 1fr);
            row-gap: $separacion / 2;
            margin-bottom: $separacion / 2;
        }
        @include telefono{
            display: flex;
            flex-direction: column;
            gap: $separacion / 2;
            margin-bottom: $separacion / 2;
        }
    }
}

.certificado__index{
    border-bottom: none;
    &:last-of-type{
        grid-area: 1 / 2;
        @include tablet{
            grid-area: 2 / 1 / 3 / 3;
            width: 50%;
            margin: 0 auto;
        }
    }
}