.footer{
    background-color: $colorFondoElementos;
    padding: $separacion / 2 0;
}
.footer-container{
    display: flex;
    justify-content: space-between;
    align-items: center;
    column-gap: $separacion / 2;

    @include telefono{
        .copy{
            font-size: 1.7rem;
        }
    }


    .footer-icon-container{
        display: flex;
        column-gap: $separacion / 2.5;
        a{
            font-size: $separacion / 1.5;
        }
    }
}