.sobre-mi{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: $separacion 0;

    @include tablet{
        flex-direction: row;
        column-gap: $separacion / 2;
        align-items: center;
    }

    @include desktop{
        flex-direction: row;
        column-gap: $separacion / 2;
        align-items: center;
    }
}
.sobre-mi-img__container{
    width: 30rem;

    @include tablet{
        width: 75rem;
    }

    @include desktop{
        width: 75rem;
    }

    .sobre-mi__img{
        border-radius: 50%;
    }
}
.sobre-mi__info{
    margin-top: 2rem;
    @include desktop{
        margin-top: 0;
    }
    @include tablet{
        margin-top: 0;
    }
    .sobre-mi__info__title{
        @include telefono{
            text-align: center;
        }
    }
    
    .sobre-mi__info__text{
        line-height: 3rem;
        margin: $separacion / 2 0;
    }
}