.certificados{
    background-color: $colorFondoElementos;
    border-radius: 1rem;
    padding: $separacion 0;

    .cert-title{
        text-align: center;
        margin-bottom: $separacion;
    }
    .certificado{
        padding-bottom: 2rem;
        margin-bottom: 1rem;
        border-bottom: 1px solid $grisClaro;
        &:last-of-type{
            border-bottom: none;
            padding-bottom: 0;
            margin-bottom: 0;
        }
    }
}
.overlay{
    position: fixed;
    background-color: rgba($color: #000000, $alpha: .5);
    width: 100%;
    height: 100%;
    top: 0;
    display: grid;
    place-items: center;
    .img-overlay{
        max-width: 96rem;
        width: 100%;
    }
    .btn-cerrar{
        cursor: pointer;
        background-color: $blanco;
        color: $negro;
        padding: 1rem 1.5rem;
        border-radius: 50%;
        margin-top: 1.5rem;
        transition: all 150ms ease-out;
        &:hover{
            background-color: $grisClaro;
            color: $blanco
        }
    }
}