.autenticar{
    background-color: $colorFondoElementos;
    border-radius: 1rem;
    padding: $separacion $separacion / 2;
    height: max-content;

    .autenticar-title{
        text-align: center;
        margin-bottom: $separacion;
    }
}
.autenticar-bloque{
    border-bottom: 1px solid $grisClaro;
    margin-bottom: $separacion;
    &:last-of-type{
        margin-bottom: 0;
        border-bottom: none;
    }

    .autenticar-bloque__info{
        margin: $separacion / 2 0;
        .perfil-code{
            color: $colorEnlaces;
        }
    }

    .autenticar-bloque__list{
        margin-bottom: $separacion / 2;
        margin-left: $separacion / 2;
        .autenticar-bloque__li{
            margin-bottom: $separacion / 2;
            .validar-cert-google{
                color: $colorEnlaces;
            }
        }
    }
}
