.webp .header-i{
    background-image: url('../img/Header3.webp');
}
.no-webp .header-i{
    background-image: url('../img/Header3.jpg');
}
.header-i{
    background-position: center center;
    background-size: cover;
    background-repeat: no-repeat;
    max-width: 100%;
    height: 70rem;
    position: relative;

    .color-header{
        background-color: rgba(0, 0, 0, .6);
        height: 100%;
    }

    .nav-bg-i{
        padding-top: $separacion / 2;
    }

}
.header-text{
    height: 73%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .header__text__info{
        font-size: 4.5rem;
    }

    @include telefono{
        .header__text__info{
            font-size: 4.4rem;
        }
    }

    .header__text__info--normal{
        font-weight: $light;
        background-color: $azulClaro;
        padding: 0 1rem;
        border-radius: 1rem;
    }

    .tablet__header__text{
        display: none;
    }

    .phone__header__text{
        display: inline-block;
    }

    @include tablet{
        height: 90%;
        .tablet__header__text{
            display: inline-block;
        }
        .phone__header__text{
            display: none;
        }
    }

    @include desktop{
        height: 90%;
        .tablet__header__text{
            display: inline-block;
        }
        .phone__header__text{
            display: none;
        }
    }
}
.nav-container{
    display: flex;
    flex-direction: column;
    align-items: center;

    @include tablet{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }

    @include desktop{
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
    }
}

.logo{
    font-weight: $light;

    .logo__bold{
        font-weight: $bold;
    }
}
.nav{
    display: flex;
    flex-direction: column;
    row-gap: 2rem;
    margin-top: 1rem;

    @include tablet{
        gap: 2rem;
        row-gap: 0;
        flex-direction: row;
        margin-top: 0mm;
    }

    @include desktop{
        gap: 2rem;
        row-gap: 0;
        flex-direction: row;
        margin-top: 0mm;
    }

    .nav__btn{
        text-align: center;
    }

}
